<template>

  <div class="sysmgmDiv">
    <el-button class="addBtn" type="primary" @click="onAdd">新增</el-button>

    <div class="editDiv el-col el-col-12 el-col-xs-12 el-col-sm-12" style="display:none">
      <el-form ref="form" :model="editData" label-width="80px">
        <el-form-item label="系统名称">
          <el-input v-model="editData.systemName"></el-input>
        </el-form-item>
        <el-form-item label="系统编码">
          <el-input v-model="editData.systemCode"></el-input>
        </el-form-item>
        <el-form-item label="责任人">
          <el-input v-model="editData.principalUser"></el-input>
        </el-form-item>
        <el-form-item label="系统环境" >
          <div v-for="(env,index) in editData.envList" style="text-align:left;">
              <el-input v-model="env.envName" v-bind:value="env.envName" @change="addNewEnv(index,env)" style="width:150px;"></el-input>
               ---
              <el-input v-model="env.envAddress" v-bind:value="env.envAddress" @change="addNewEnv(index,env)" style="width:150px;"></el-input>

              <el-button type="primary" icon="delete" @click="deleteEnv(env)"></el-button>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">
            <span v-if="action=='edit'" >修改</span>
            <span v-if="action=='save'" >保存</span>
          </el-button>
          <el-button @click="onCancel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
      :data="tableData"
      class="sysmgmTable"
      props="editData"
      border
      style="width: 100%">
      <el-table-column
        label="系统code"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.systemCode }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="系统名称"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.systemName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="负责人"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.principalUser }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="环境列表"
        width="180">
        <template scope="scope">
          <ul>
            <li v-for="env in scope.row.envList">
              {{ env.envName }}:{{ env.envAddress }}
            </li>
          </ul>
        </template>
      </el-table-column>
      <el-table-column
        label="更新时间"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.gmtCreate }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button @click="handleEdit(scope.$index, scope.row)" size="small" >编辑</el-button>

          <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios';
  import EditForm from '@/components/EditForm'

  export default {
    data: () => ({
    tableData: [],
      errors: [],
    editData:{},
    action:"edit"
    }),
    created() {
    axios.get(`/system/page?systemCode=&page=0&length=10`)
//    axios.get(`http://jsonplaceholder.typicode.com/posts`)
        .then(response => {
//      debugger
        // JSON responses are automatically parsed.
//      response = {"body":{"data":[{"envList":[{"envAddress":"qd","envName":"sdf","gmtCreate":"2017-05-08 21:23:26.0","id":7,"systemCode":"adf"},{"envAddress":"","envName":"","gmtCreate":"2017-05-08 21:23:26.0","id":8,"systemCode":"adf"}],"gmtCreate":"2017-05-08 21:23:25.0","id":2,"principalUser":"asdf","systemCode":"adf","systemName":"asd"},{"envList":[],"gmtCreate":"2017-05-08 21:19:55.0","id":1,"principalUser":"feixue","systemCode":"mBridge","systemName":"鹊桥"}],"size":2},"success":true};
        this.tableData = response.data.body.data;
    })
    .catch(e => {
        this.errors.push(e)
    })},
    methods: {
      handleEdit(index, row){
        this.editData = row;
        document.getElementsByClassName("editDiv")[0].style.display = "";
        document.getElementsByClassName("sysmgmTable")[0].style.display = "none";
      },
      handleDelete(index, row) {
        axios.post('/system/delete?systemCode='+row.systemCode)
          .then(function(response){
            console.log('delete successfully');
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onSubmit() {
        axios.post('/system/save', this.editData)
          .then(function(response){
            console.log('saved successfully');
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onSave(){
        axios.post('/system/save', this.editData)
          .then(function(response){
            console.log('saved successfully');
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onCancel(){
        document.getElementsByClassName("editDiv")[0].style.display = "none";
        document.getElementsByClassName("sysmgmTable")[0].style.display = "";
        var addBtn = document.getElementsByClassName("addBtn")[0];
        if(addBtn){
          addBtn.style.display = "";
        }
      },
      onAdd(){
        this.editData = {
          "envList": [
            {
              "id": 0,
              "systemCode": "",
              envName: "",
              "envAddress": "",
              "gmtCreate": "",
              "gmtModify": ""
            }
          ],
          "gmtCreate": "",
          "gmtModify": "",
          "id": 0,
          "principalUser": "",
          "systemCode": "",
          "systemName": ""
        };
        this.action = "save";
        document.getElementsByClassName("editDiv")[0].style.display = "";
        document.getElementsByClassName("sysmgmTable")[0].style.display = "none";
        document.getElementsByClassName("addBtn")[0].style.display = "none";
      },
      addNewEnv(index,value){
        var envList = this.editData.envList;

        if(envList.length-1 == index){
          this.editData.envList.push({
            "id": 0,
            "systemCode": "",
            "envName": "",
            "envAddress": "",
            "gmtCreate": "",
            "gmtModify": ""
          });
        };
      },
      deleteEnv(env){
        var index = this.editData.envList.indexOf(env);
        if(this.editData.envList.length==1)return;
        this.editData.envList.splice(index,1);
      }
    }}
</script>

<style>
  .sysmgmDiv{position:relative;overflow:hidden;}
  .sysmgmTable{margin-top:40px;}
  .editDiv{margin-top:20px;}
  .sysmgmDiv .addBtn{position:absolute;left:0;top:0;}
</style>



